/* src/pages/Register.css */

.register-container {
  width: 300px;
  margin: 100px auto;
  padding: 20px;
  border: 1px solid #E7B2A4;
  border-radius: 5px;
  background-color: #eac8be;
}

.register-container h2 {
  text-align: center;
}

.input-group {
  margin: 10px 0;
}

.input-group label {
  display: block;
  margin-bottom: 5px;
}

.input-group input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

button {
  width: 100%;
  padding: 10px;
  background-color: #333;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #555;
}
